.experienceSection {
    overflow: auto;
    padding-bottom: 35px;
    background-image: url('data:image/svg+xml;utf8,<svg width="10" height="10" xmlns="http://www.w3.org/2000/svg"><rect x="4" y="4" width="2" height="2" fill="%23E9E9E9" /></svg>');
    background-size: 8px 8px;
}

.experienceContent {
    display: flex;
    width: 70%;
    margin: 0 auto; /* Center the content */
    gap: 50px;
}

@media (max-width: 900px) {
    .experienceContent {
        display: flex;
        flex-direction: column;
        width: 70%;
        margin: 0 auto; /* Center the content */
        gap: 50px;
    }
  }

.companyNames {
    display: flex;
    flex-direction: column;
    border-left: solid rgb(127 125 125 / 1);
    justify-content: space-evenly;
}

@media (max-width: 900px) {
    .companyNames {
        display: flex;
        flex-direction: row;
        border-left: none;
        border-bottom: solid rgb(127 125 125 / 0.75rem);
        justify-content: space-evenly;
    }
  }

.tab-button {
    width: 9.4rem;
    height: 100%;
    border: none;
    display: inline-block;
    cursor: pointer;
    padding: 1rem;
    color: #343a40;
    font-weight: medium;
    font-size: 16px;
    text-align: left;
    text-transform: capitalize;
    transition: .3s ease-in;
    background-color: transparent;
    transition:  background-color 0.5s ease, color 0.5s ease;
}

.tab-button:hover {
    background-color: lightgray;
    color: white;
}

.tab-button-selected {
    width: 9.4rem;
    height: 100%;
    border: none;
    display: inline-block;
    text-align: left;
    cursor: pointer;
    padding: 1rem;
    color: #fff;
    background-color: #81878c;
    font-weight: medium;
    font-size: 16px;
    text-transform: capitalize;
    border-left: 3px solid #7f7d7d;
    transition: .3s ease-in;
}

.experiencePoints {
    list-style-type: circle;
}
.experiencePoint {
    margin-bottom: 10px;
}


  