.projectsContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 70%;
    gap: 50px;
    row-gap: 70px;
    margin: 0 auto;
    padding-top: 30px;
}

@media (max-width: 900px) {
    .projectsContainer {
        display: grid;
        grid-template-columns: 1fr;
        width: 70%;
        row-gap: 70px;
        margin: 0 auto;
        padding-top: 30px;
    }     
  }

.projectImage {
    height: 170px;
    padding: 20px;
    max-width: 100%;
    vertical-align: middle;
    overflow-clip-margin: content-box;
    overflow: clip;
    transition: transform 0.5s ease;
}

.project:hover{
    transform: translateY(-10px);
}

.projectTitle {
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 1.75rem;
    margin: 0;
    padding-top: .5rem;
    padding-bottom: .5rem;
}

.projectTech {
    font-weight: 600;
    margin-top: 1.25rem;
    margin-bottom: .75rem;
    margin: 0;
}

.projectLink {
    display: flex;
    align-items: center;
    gap: .75rem;
}

.projectAnchor {
    display: flex;
    align-items: center;
    color: inherit;
    text-decoration: inherit;
    padding-top: .75rem;
    padding-bottom: .75rem;
    padding-right: .5rem;
    gap: .5rem;
}

.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    transition: background-color 0.5s ease;
}
.project:hover {    
    background-color: rgb(233 236 239 /1)
}

.projectdesc {
    padding: 10px;
}


