.summary {
    font-size: 1.25rem;
    line-height: 1.75rem;
    text-align: center;
    margin: 0 auto;
    width: 75%;
}

.techText {
    font-weight: 600;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 0;
}

.list {
    margin: 0 auto;
    list-style-type: circle;
    display: grid;
    column-gap: 20px;
    grid-template-columns: 1fr 1fr;
    max-width: 420px;
    font-size: 16px;
    row-gap: 5px;
}