a {
    color: inherit;
    text-decoration: inherit;
  }
  
  .aboutMe {
    overflow: auto;
    margin-top: 40px;
    padding-top: 182px;
    padding-bottom: 60px;
    background-image: url('data:image/svg+xml;utf8,<svg width="10" height="10" xmlns="http://www.w3.org/2000/svg"><rect x="4" y="4" width="2" height="2" fill="%23E9E9E9" /></svg>');
    background-size: 8px 8px;
  }
  
  .contact {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 65%;
    left: 3%;
    gap: 20px;
  }
  
  .content {
    width: 70%;
    display: flex;
    margin: 0 auto; /* Center the content */
  }

  @media (max-width: 900px) {
    .content {
      width: 70%;
      display: flex;
      margin: 0 auto; /* Center the content */
      flex-direction: column;
    }     
  }

  .displayImage {
    position: relative;
    display: flex;
    justify-content: center;
    width: 60%;
    height: 400px;
    border-radius: 50%;
    z-index: 1;
    margin: 0 auto;
  }
  
  .displayImage:before {
    content: "";
    background-color: #46474A;
    position: absolute;
    border-radius: 50%;
    width: 340px;
    height: 100%;
    z-index: -1;
  }
  
  .displayImage:after {
    content: "";
    background-color: #46474A;
    position: absolute;
    border-radius: 50%;
    width: 340px;
    top: 10px;
    height: 100%;
    z-index: -2;
  }
  
  .myImage {
      height: 100%;
      border-radius: 50%;
  }
  
  .desc {
    width: 40%;
    margin: 0 auto;
  }

  @media (max-width: 900px) {
    .desc {
      width: 50%;
      margin: 0 auto;
    }
  }

  .jobTitle {
    font-size: 24px;
    font-weight: 900;
  }

  .jobTitle::after {
    content: '|';
    animation: cursor 1.1s infinite step-start;
  }
  
  @keyframes cursor {
    50% {
      opacity: 0;
    }
  }
  
  .caption {
    font-weight: 600;
    font-size: .875rem;
    line-height: 1.25rem;
  }
  
  .touchButton {
    height: 35px;
    background: transparent;
    border: 1px solid rgb(52 58 64);
    border-radius: 9px;
    font-weight: 600;
    font-size: .875rem;
    transition:  background-color 0.5s ease, color 0.5s ease;
  }

  .touchButton:hover {
    cursor: pointer;
    background-color: rgb(52 58 64);
    color: white;
  }